body {
  font-family: 'Poppins', sans-serif;
  color: #fff;
  background: #000;
  font-size: 15px;
  line-height: 26px;
  font-weight: 400;
  letter-spacing: 0.5px;
  overflow-x: hidden;
}

a {
  color: #fff;
  text-decoration: none;
}


.btn{
  font-size: 14px;
  padding: 10px 22px;
  border-radius: 0px;
  cursor: pointer;
  font-weight: 400;
  color: #fff;
  border-radius: 30px;
  text-transform: capitalize;
  -webkit-transition: all 0.2s linear;
  -moz-transition: all 0.2s linear;
  -o-transition: all 0.2s linear;
  transition: all 0.2s linear;
  display: inline-block;
}
.btn:focus,
.btn:active{
box-shadow: none;
outline: none;
color: #fff;
}
.btn-common{
font-size: 14px;
  padding: 10px 30px;
  border-radius: 0px;
  cursor: pointer;
  font-weight: 400;
  color: #fff;
  border-radius: 30px;
  text-transform: uppercase;
  -webkit-transition: all 0.2s linear;
  -moz-transition: all 0.2s linear;
  -o-transition: all 0.2s linear;
  transition: all 0.2s linear;
  display: inline-block;
background-color: #ff6600;
position: relative;
z-index: 1;
}
button:focus{
outline: none !important;
}
.btn.btn-secondary{
  background: #ff6600;
  border-color: #ff6600;
  color: #fff;
}
.btn.btn-secondary:hover{
  box-shadow: 1px 5px 12px rgba(0,0,0, 0.15);
}
.btn-secondary.banner_btn{
  background: #ff6600;
  border-color: #ff6600;
color: #fff;
  text-transform: capitalize;
  margin-top: 30px;
padding: 12px 32px;
}

/* Default navbar styling */
.navbar {
  background-color: #5e5e5f; /* Dark background color */
  padding: 0.5rem 1rem;
  color: #ccc !important; 
}

.navbar-brand span {
  color: #ff6600; /* Brand text color */
  margin: 0; /* Remove default margin */
}

.navbar-brand .typed-cursor {
  color: #f0f1ef; /* Cursor color */
}


.navbar-nav .nav-link {
  color: #ffffff; /* Nav link color */
  transition: color 0.3s ease; /* Smooth color transition */
}

.navbar-nav .nav-link:hover,
.navbar-nav .nav-link:focus {
  color: #f8f9fa; /* Lighter color on hover/focus */
}

/* Style for active link */
.navbar-nav .nav-item.active .nav-link {
  color: #ff6600; /* Highlight color for active link */
  font-weight: bold; /* Optional: bold text for active link */
}

/* Style for toggler */
.navbar-toggler {
  border-color: rgba(214, 205, 205, 0.1); /* Light border color */
}

/* Toggler icon styling */
.navbar-toggler-icon {
  display: inline-block;
  width: 1.5em;
  height: 1.5em;
  vertical-align: middle;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100%;
}

/* Media queries */

/* For tablets and above (min-width: 768px) */
@media (min-width: 768px) {
  .navbar-nav {
    flex-direction: row; /* Ensure items are in a row */
  }

  .navbar-collapse {
    justify-content: flex-end; /* Align items to the right */
  }
}

/* For mobile devices (max-width: 767px) */
@media (max-width: 767px) {
  .navbar-nav {
    flex-direction: column; /* Stack items vertically */
    text-align: center; /* Center align text */
  }

  .navbar-nav .nav-item {
    margin: 0; /* Remove margin between items */
  }

  .navbar-toggler {
    display: block; /* Show toggler on mobile */
  }

  .navbar-collapse {
    width: 100%; /* Full width on mobile */
  }
  
}




