/* General styles for .services-item */
.services-item {
  border: 1px solid #ccc;
  background: #343a40; /* Dark background */
  border-radius: 5px;
  margin-bottom: 30px;
  padding: 20px;
  text-align: center;
  transition: all 0.3s ease-in-out;
  height: 300px; /* Fixed height for consistent item sizes */
}

.section-title{
  margin: 80px;
}

/* Dark background variations */
.dark_bg .services-item {
  border: 1px solid #fff; /* White border on dark background */

}

/* Hover effect for services-item */
.services-item:hover {
  transform: translateY(-10px); /* Moves item up on hover */
}

/* Icon styling */
.services-item .icon {
  background-size: contain; /* Ensure the background image is contained within the element */
  width: auto; /* Adjusted width */
  height: auto; /* Adjusted height */
  margin: 0 auto; /* Center the icon */
  display: flex;
  justify-content: center;
  align-items: center;
  background: transparent; /* Removed background image */
}

/* Image inside icon */
.services-item .icon img {
  max-width: 100%; /* Ensure the image fits within the container */
  height: auto; /* Maintain aspect ratio */
}

/* Heading styling within services-item */
.services-item .services-content h3 {
  margin: 10px 0; /* Margin for headings */
}

/* Link styling within headings */
.services-item .services-content h3 a {
  text-transform: uppercase;
  font-size: 18px;
  color:#ff6600;
  transition: all 0.3s ease-in-out;
}

/* Dark background link color */
.dark_bg .services-item .services-content h3 a {
      
       color: #ff6600;
}

/* Hover effect for link */
.services-item:hover .services-content h3 a {
  color: white;
}



/* Portfolio section styling */
.portfolio-items {
  display: flex;
  flex-wrap: wrap;
  margin: -10px;
}

.portfolio-items .mix {
  padding: 10px;
  /* Hidden by default */
}

.portfolio-items .shot-item {
  margin: 0 0 150px 0;
  border-radius: 4px;
  background: #fff;
  position: relative;
}

.portfolio-items .portfolio-img {
  overflow: hidden;
  position: relative;
}

.portfolio-items .portfolio-img img {
  width: 100%;
  display: block;
}

.portfolio-items .overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: rgba(8, 214, 101, 0.7); /* Semi-transparent green background */
  opacity: 0;
  transition: all 0.5s ease-in-out;
}

.portfolio-items .shot-item:hover .overlay {
  opacity: 1;
}

.portfolio-items .overlay .icons {
  position: absolute;
  color: #fff;
  font-size: 20px;
  line-height: 42px;
  text-align: center;
  border-radius: 50%;
  background: #08d665;
  padding: 8px;
  cursor: pointer;
  
}

.portfolio-items .overlay .preview {
  left: 50%;
  transform: translateX(-50%);
  top: 50%;
  margin-top: -21px;
}

.portfolio-items .overlay .link {
  right: 50%;
  transform: translateX(50%);
  top: 50%;
  margin-top: -21px;
}

.portfolio-items .shot-content {
  padding: 15px;
}

.portfolio-items .shot-content h3 {
  margin-top: 0;
  font-size: 18px;
}

.portfolio-items .shot-content p {
  font-size: 14px;
  color: #333;
}

/* Active filter button styling */
.controls .filter.active {
