/* Blog Area Styles */
.blog-area {
  padding: 80px 0;
  background-color: #111; /* Dark background for the blog area */
}

.section-title {
  text-align: center;
  margin-bottom: 50px;
  color: #fff; /* White color for the section title */
}

.section-title span {
  color: #ff6600; /* Accent color for the title */
}

/* Single Blog Styles */
.single_blog {
  background-color: #292929;
  padding: 20px;
  margin-bottom: 30px;
  border-radius: 5px;
  transition: transform 0.3s ease;
  text-align: center; /* Center align text and elements */
}

.single_blog:hover {
  transform: translateY(-5px); /* Slight lift on hover */
}

.single_blog .blog-thumb {
  position: relative;
}

.single_blog .blog-thumb img {
  width: 200px; /* Fixed width for the image */
  height: auto; /* Maintain aspect ratio */
  border-radius: 5px;
  display: block;
  margin: 0 auto; /* Center the image */
}

.single_blog .blog-info {
  margin-top: 15px;
}

.single_blog h4 {
  font-size: 18px;
  margin-bottom: 10px;
  color: #fff;
}

.single_blog p {
  font-size: 14px;
  color: #ccc; /* Light gray for paragraph text */
}

.single_blog small {
  font-size: 12px;
  color: #aaa; /* Lighter gray for small text */
  display: block;
  margin-top: 5px;
}

.single_blog .icon img {
  width: 200px;
  height: auto;
  border-radius: 5px;
  margin-top: 10px; /* Add some margin on top */
  display: block;
  margin: 10px auto 0 auto; /* Center the icon */
}

.blog_btn {
  margin-top: 10px;
  color: #fff;
  background-color: #ff6600;
  border: none;
  transition: background-color 0.3s ease;
}

.blog_btn:hover {
  background-color: #e67e22;
}

/* Media Queries */
@media (max-width: 767px) {
  .section-title {
    font-size: 24px;
  }

  .single_blog h4 {
    font-size: 16px;
  }

  .single_blog p {
    font-size: 12px;
  }
}










